@mixin snowflake {
  background-image: url('../../../../../../public/assets/svg/snowflake.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

@mixin randomBlur {
  $blur: random(
    $limit: 2,
  );
  filter: blur($blur#{px});
}

@mixin snowflakeGenerator {
  content: '';
  @include snowflakeRoad;
  display: inline-block;
  @if (random($limit: 10) < 5) {
    @include snowflake;
    @if (random($limit: 10) < 5) {
      @include randomBlur;
    }
  } @else {
    background-color: rgba(185, 224, 255, 0.795);
    border-radius: 50%;
    @include randomBlur;
  }

  $size: random(
    $limit: 12,
  );
  height: $size#{px};
  width: $size#{px};
}

@mixin showBlock {
  animation: opacityBlock 3s ease-in-out;
  @keyframes opacityBlock {
    0% {
      opacity: 0.01;
    }

    30% {
      opacity: 0.05;
    }

    100% {
      opacity: 1;
    }
  }
}

@mixin snowflakeRoad {
  $time: random(
    $limit: 3,
  );
  animation: fall $time#{s} infinite linear;

  $deley: random(
    $limit: 2000,
  );
  animation-delay: $deley#{'ms'};

  @keyframes fall {
    0% {
      $leftPosition: random(
        $limit: 102,
      );
      transform: translate($leftPosition#{'%'}, -50%);
    }

    100% {
      $leftPosition: random(
        $limit: 102,
      );
      transform: translate($leftPosition#{'%'}, 6002%);
    }
  }
}

.snow-fall {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: absolute;
  padding-top: -20px;

  @include showBlock;

  @for $i from 1 to 131 {
    &__frame_#{$i}:before {
      @include snowflakeGenerator;
    }

    &__frame_#{$i}::after {
      @include snowflakeGenerator;
    }
  }
}
