// Colors
$font-color: rgb(240, 240, 240);
$background-color: rgba(25, 47, 45, 1);
$border-color: rgba(36, 197, 219, 1);
$filter-favorite-color: rgb(120, 221, 117);
$switch-off-color: rgb(245, 68, 68);

$garland-red-color: rgb(240, 65, 65);
$garland-blue-color: rgb(103, 164, 255);
$garland-gold-color: rgb(255, 225, 56);
$garland-silver-color-fill: rgb(243, 243, 243);
$garland-silver-color: linear-gradient(
  45deg,
  rgb(170, 170, 170),
  rgb(255, 255, 255) 50%,
  rgb(170, 170, 170) 100%
);
$garland-multi-color: linear-gradient(
    217deg,
    rgba(255, 0, 0, 1),
    rgba(255, 0, 0, 0.1) 75%
  ),
  linear-gradient(127deg, rgba(0, 255, 0, 1), rgba(0, 255, 0, 0.1) 75%),
  linear-gradient(336deg, rgba(0, 0, 255, 1), rgba(0, 0, 255, 0.1) 75%);

// Sizes
$max-screen-width: 2560px;

$border-size: 0.02rem;
$border-radius-size: 0.33rem;

$standart-font-size: 1rem;
$title-size: 1.8rem;

$form-title-size: 1.3rem;

$link-image-height: 2rem;

$header-height: 5rem;
$footer-height: 5rem;

$compound-padding: 30px;

$home-max-size-block: 21rem;

$toys-list-wrap: 0.5rem;

$toy-card-width: 222px;
$toy-card-height: 342px;
$toy-card-laptop-width: 30%;
$toy-card-laptop-height: auto;
$toys-border-radius: 10px;
$toys-padding: 0.3rem;

$tree-container: 160px;
$card-2-in-line: 134px;

$choice-gap: 4px;

$choice-background-height: 64px;
$choice-garland-size: 20px;

$switch-heigth: 34px;
$switch-width: 60px;
$switch-padding: 4px;

$toys-choice-height: 75px;

$toy-size: 75%;
$toy-size-on-tree: 45px;

// Properties
$toy-card-backdrop-filter: blur(4px);
$toys-card-background: radial-gradient(
  109.56% 109.56% at 0% -2.94%,
  rgba(255, 255, 255, 0.5) 0%,
  rgba(179, 246, 255, 0.5) 100%
);
$toys-card-favorite-background: radial-gradient(
  109.56% 109.56% at 0% -2.94%,
  rgba(255, 245, 106, 0.5) 0%,
  rgba(252, 255, 73, 0.699) 100%
);

$toys-on-load-img-gradient: repeating-linear-gradient(
  130deg,
  rgb(102, 102, 102),
  rgb(221, 221, 221),
  rgb(102, 102, 102)
);

$search-background: radial-gradient(
  118.88% 606.86% at 0% 0%,
  rgba(36, 197, 219, 0.15) 0%,
  rgba(36, 197, 219, 0) 100%
);

$choice-background: radial-gradient(
  109.56% 109.56% at 0% -2.94%,
  rgba(255, 255, 255, 0.5) 0%,
  rgba(179, 246, 255, 0.5) 100%
);

$candy-cane-background: repeating-linear-gradient(
  -60deg,
  white,
  white 6px,
  red 7px,
  red 14px
);

// font-family
$head-font-family: 'Neucha', cursive;
