@import '../../../../styles/mixin.scss';
@import '../../../../styles/constants.scss';

.choice-block {
  &__title {
    @include title-style;
  }

  &__container {
    @include tree-choice-container;
  }

  &__label {
    @include choice-label;
  }

  &__card {
    border-radius: $border-radius-size;
    background: $choice-background;

    &_1-in-line {
      width: 100%;
    }

    &_2-in-line {
      flex: 1 1 48%;
    }

    &_4-in-line {
      flex: 1 1 24%;
    }

    &_5-in-line {
      flex: 1 1 19%;
    }
  }
}

.choice-block__input:checked ~ .choice-block__label {
  border-radius: $border-radius-size;
  border: $font-color solid $border-size;
}
