@import '../../../../../styles/constants.scss';
.garland {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: stretch;
  clip-path: polygon(
    47% 0,
    55% 0,
    100% 80%,
    79% 95%,
    65% 100%,
    35% 100%,
    20% 95%,
    0 80%
  );

  &__line {
    overflow: visible;
  }

  &__lamp {
    animation: lampAnimation 3s ease infinite;
    &_silver {
      fill: $garland-silver-color-fill;
    }
    &_red {
      fill: $garland-red-color;
    }
    &_blue {
      fill: $garland-blue-color;
    }
    &_gold {
      fill: $garland-gold-color;
    }
    &_multi {
      animation: multiLampAnimation 3s ease infinite;
    }

    @for $i from 1 to 15 {
      &_#{$i} {
        $deley: random(
          $limit: 2000,
        );
        animation-delay: $deley#{'ms'};
      }
    }
  }
}

@keyframes lampAnimation {
  0% {
    opacity: 0.5;
  }

  20% {
    opacity: 0.8;
  }

  40% {
    opacity: 0.5;
  }

  60% {
    opacity: 0.3;
  }
  80% {
    opacity: 1;
  }
  100% {
    stroke-opacity: 0.5;
  }
}

@keyframes multiLampAnimation {
  0% {
    fill: $garland-silver-color-fill;
    opacity: 0.5;
  }

  20% {
    opacity: 0.8;
    fill: $garland-red-color;
  }

  40% {
    opacity: 0.5;
    fill: $garland-gold-color;
  }

  60% {
    opacity: 0.3;
    fill: $garland-blue-color;
  }
  80% {
    opacity: 1;
    fill: $filter-favorite-color;
  }
  100% {
    fill: $garland-silver-color-fill;
    stroke-opacity: 0.5;
  }
}
