@import '../../../../styles/constants.scss';

.decoration-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  &__ghost-tree {
    position: relative;
    height: 90%;
    width: 50%;
    clip-path: polygon(
      44% 0,
      56% 0,
      100% 80%,
      79% 95%,
      65% 100%,
      35% 100%,
      20% 95%,
      0 80%
    );
  }

  &__toy {
    position: absolute;
    top: 30px;
    left: 30px;
    height: $toy-size-on-tree;
    width: $toy-size-on-tree;
    cursor: grab;
  }
}
