@import '../../../styles/constants.scss';
@import '../../../styles/mixin.scss';

.tree-container {
  position: relative;
  display: grid;
  // variables in grid-tempale don't work for some reason
  grid-template: 1fr / 384px 1fr 384px;
  grid-template-areas: 'left center right';

  &__item {
    z-index: 1;

    &_center {
      grid-area: center;
      height: 100%;
    }

    &_left,
    &_right {
      padding: 2% 6%;
    }

    &_left {
      grid-area: left;
    }

    &_right {
      grid-area: right;
    }
  }

  &__button {
    @include button-toys;
    margin-right: $border-radius-size;
  }

  &::after {
    z-index: 0;
    position: absolute;
    display: block;
    background: url('../../../../public/assets/bg.jpg'),
      linear-gradient(130deg, $background-color 0%, $background-color 100%);
    background-repeat: no-repeat;
    background-size: cover;
    background-blend-mode: luminosity;
    background-position: center;
    height: 100%;
    width: 100%;
    content: '';
    filter: blur(5px);
  }

  @for $i from 1 to 11 {
    &__background_#{$i}.choice-block__card {
      height: $choice-background-height;
    }
    &__background_#{$i} {
      background-image: url('../../../../public/assets/bg/#{$i}.jpg');
      background-size: cover;
      background-position: bottom;
    }
  }

  @for $i from 1 to 7 {
    &__tree_#{$i}.choice-block__card {
      height: $card-2-in-line;
    }
    &__tree_#{$i} {
      background: url('../../../../public/assets/tree//#{$i}.png'),
        $choice-background;
      background-size: 50%, 100%;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__garland {
    &_multi.choice-block__card,
    &_red.choice-block__card,
    &_blue.choice-block__card,
    &_gold.choice-block__card,
    &_silver.choice-block__card {
      border-radius: 50%;
      height: $choice-garland-size;
      width: $choice-garland-size;
    }

    &_multi.choice-block__card {
      background: $garland-multi-color;
    }

    &_red.choice-block__card {
      background: $garland-red-color;
    }

    &_blue.choice-block__card {
      background: $garland-blue-color;
    }

    &_gold.choice-block__card {
      background: $garland-gold-color;
    }

    &_silver.choice-block__card {
      background: $garland-silver-color;
    }
  }
}

@media screen and (max-width: 1088px) {
  .tree-container {
    grid-template: 1fr / 300px 1fr 300px;
    grid-template-areas: 'left center right';
  }
}

@media screen and (max-width: 940px) {
  .tree-container {
    grid-template: 1fr / 200px 1fr 200px;
    grid-template-areas: 'left center right';
  }
}

@media screen and (max-width: 820px) {
  .tree-container {
    grid-template: 1fr 1fr / 1fr 1fr;
    grid-template-areas: 'center center' 'left right';
  }
}
