@import '../../../../../styles/constants.scss';

.switch-container {
  background: $font-color;
  border: $background-color solid $border-size;
  height: $switch-heigth;
  width: $switch-width;
  padding: $switch-padding;
  border-radius: $switch-heigth;
  cursor: pointer;

  &__input {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;

    &:checked ~ .switch-container__switch {
      grid-template-columns: 1fr 1fr 0fr;
      &::after {
        background-color: $filter-favorite-color;
      }
    }
  }

  &__switch {
    height: 100%;
    grid-template-columns: 0fr 1fr 1fr;
    display: grid;
    transition: all 0.53s;

    &::after {
      grid-column: 2;
      border-radius: 50%;
      content: '';
      background: $switch-off-color;
      transition: background 0.53s;
    }
  }
}
