@import './constants.scss';

@mixin block-start-setting {
  padding: $compound-padding;
}

@mixin line-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin button-backgound {
  border: $border-color solid $border-size;
  color: $font-color;
  background: rgba($color: $background-color, $alpha: 0.3);
  border-radius: $title-size;
}

@mixin button-toys {
  @include button-backgound;
  margin: $standart-font-size 0;
  padding: $border-radius-size $link-image-height;
  background: $search-background;
  user-select: none;
  cursor: pointer;
}

@mixin filter-element {
  @include button-backgound;
  background: $search-background;
  padding: $border-radius-size;
}

@mixin title-style {
  font-size: $standart-font-size;
  font-family: $head-font-family;
  color: $font-color;
}

@mixin tree-choice-container {
  display: flex;
  gap: $choice-gap;
  margin: $border-radius-size 0;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-height: $tree-container;
  overflow-y: auto;

  &::-webkit-scrollbar {
    background-color: transparent;
  }
}

@mixin choice-label {
  display: block;
  height: 100%;
  width: 100%;
  cursor: pointer;
}
