@import '../../../../../styles/mixin.scss';
@import '../../../../../styles/constants.scss';

.toys-collection {
  &__title {
    @include title-style;
  }

  &__container {
    @include tree-choice-container;
    height: 100%;
  }

  &__item {
    position: relative;
    height: $toys-choice-height;
    width: $toys-choice-height;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: $border-radius-size;
    background: $choice-background;
    cursor: grab;
  }

  &__img {
    height: $toy-size;
    width: $toy-size;
    user-select: none;

    &_empty {
      filter: grayscale(1);
      cursor: not-allowed;
    }
  }

  &__item-cont {
    position: absolute;
    bottom: 0;
    right: 0;
    height: $form-title-size;
    width: $form-title-size;
    display: block;
    line-height: $form-title-size;
    border-radius: 50%;
    background-color: $border-color;
    color: $font-color;
    text-align: center;
    user-select: none;
  }
}
