@import '../../../../../styles/constants.scss';

.img-button {
  height: $link-image-height;
  width: $link-image-height;
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: transform 0.53s;

  &:hover {
    transform: scale(1.1);
  }

  &:active {
    transform: scale(1.2);
  }

  &_snow,
  &_snow_active {
    background-image: url('../../../../../../public/assets/svg/snow.svg');
  }

  &_snow_active {
    animation: snow-fall 15s infinite;
  }

  &_audio {
    background-image: url('../../../../../../public/assets/svg/audio.svg');

    &_active {
      background-image: url('../../../../../../public/assets/svg/mute.svg');
    }
  }
}

@keyframes snow-fall {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
