@import '../../../../../styles/mixin.scss';
@import '../../../../../styles/constants.scss';

.saved-tree {
  &__title {
    @include title-style;
  }

  &__container {
    @include tree-choice-container;
  }

  &__label {
    @include choice-label;
  }

  &__card {
    display: block;
    border-radius: $border-radius-size;
    background-color: red;
    height: $toys-choice-height;
    width: $toys-choice-height;
  }
}
